.LoginScreen{
    position: relative;
    height: 100%;
    background: url('https://w7.pngwing.com/pngs/280/176/png-transparent-the-imitation-game-banner-display-advertising-film-poster-posteritati-movie-poster-gallery-display-advertising-poster-banner.png')
    center no-repeat;
    background-size: cover;
    
}
.LoginScreen__logo{
    position: fixed;
    width: 150px;
    left: 0;
    object-fit: contain;
    padding-left: 20px;
}
.LoginScreen__button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}
.LoginScreen__gradient{
    width: 100%;
    height: 100vh;
    z-index: 1;
    background: rgba(0,0,0,0.4);
    background-image: linear-gradient(to top,
    rgba(0,0,0,0.8)0,
    rgba(0,0,0,0)60%,
    rgba(0,0,0,0.8)100%

    
    );
}
.LoginScreen_body{
    
    z-index: 1;
    color: white;
    padding: 20px;
    left: 0;
    right: 0;
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.LoginScreen_body > h1 {
    
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.LoginScreen_body > h2{
    font-size: 2rem;
    font-weight: 200;
    margin-bottom: 30px;
}
.LoginScreen_body > h3{
    font-size: 1.3rem;
    font-weight: 400;
}
.LoginScreen_input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
}
.LoginScreen_getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    border: none;
    background-color: #e50914;
    font-weight: 600;
    cursor:progress;
}
.LoginScreen_input{
    margin: 20px;
}