.signupScreen{
    max-width: 300px;
    padding: 70px;
    margin-right: auto;
    margin-left: auto;
    left: 20%;
    right: 20%;
    background:rgba(0,0,0,0.85);
}
.signupScreen>form{
    display: grid;
    flex-direction: column;
}
.signupScreen>form>input{
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
}
.signupScreen>form>button{
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    border-radius:5px;
    background-color: #e50914;
    border:none;
    cursor: pointer;
    margin-top:20px ;
}
.signupScreen>form>h1{
    margin-bottom: 25px;
}
.signupScreen>form>h4{
    text-align: left;
    margin-top: 30px;
}
.signupScreen_gray{
    color: gray;
}
.signupScreen_link:hover{
    cursor: pointer;
    text-decoration: underline;
}

